.Line-button{
    /* Rectangle: */
    background: #ED1D25;
    -webkit-box-shadow: 0 3px 10px 0 rgba(0,0,0,0.28);
            box-shadow: 0 3px 10px 0 rgba(0,0,0,0.28);
    border-radius: 16px;
    font-family: Roboto-Medium, -apple-system, Arial, Helvetica, sans-serif;
    font-size: 20px;
    color: #FFFFFF;
    height: 74px;
    display: -ms-flexbox;
    display: flex;
    padding: 0 10px;
    margin-bottom: 24px;
}

.Line-button:active {
    opacity: .6;
    -webkit-transform: scale(.99);
        -ms-transform: scale(.99);
            transform: scale(.99);
}

.Line-number{
    margin-top: 10px;
    width: 74px;
    min-width: 74px;
    height: 64px;
    background-position: 74px 0px;
}

.Line-endpoints{
    margin-left: 36px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    min-width: 0;
    padding: 6px 0;
}

.Line-stop{
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}




